import { BrowserRouter, Routes, Route } from "react-router-dom";
import Main from "./pages/main/Main"; //  
import Layout from "./pages/layouts/Layout"; //  
import Introduce from "./pages/Introduce/Introduce"; //  
import Project from "./pages/project/Project"; //  
import DetailProject3on3 from "./pages/detailProject/DetailProject3on3"; //  
import DetailProject5jo from "./pages/detailProject/DetailProject5jo"; //  

function App() {
  return (
    <BrowserRouter basename={process.env.PUBLIC_URL}>
      <Routes>
        {/* Layout      Outlet     */}
        <Route path="/" element={<Layout />}>
          {/* Layout    */}
          <Route path="" element={<Main />} />
          <Route path="introduce" element={<Introduce />} />
          <Route path="project" element={<Project />} />
          <Route path="3on3" element={<DetailProject3on3 />} />
          <Route path="5jo" element={<DetailProject5jo />} />
        </Route>
      </Routes>
    </BrowserRouter>
  );
}

export default App;
